body{
  background-color: #f5f0f0;
}

.container{
  display: flex;
  align-items: center;
  justify-content: center; 
  flex-direction: column;
}

.image{
  width: 400px;
  margin-top: 50px;
}

@media(max-width: 770px){
  .container{
    margin: 0 10%;
  }

  .image{
    width: 100%;
  }
}